  src: url(/jf-fitness/static/media/Metric-Light.9f55236a.otf) format("opentype");
}

@font-face {
  font-family: 'Metric';
  font-weight: 300;
  font-style: italic;
  src: url(/jf-fitness/static/media/Metric-LightItalic.36530be2.otf) format("opentype");
}

@font-face {
  font-family: 'Metric';
  font-weight: 400;
  font-style: normal;
  src: url(/jf-fitness/static/media/Metric-Regular.928d99e3.otf) format("opentype");
}

@font-face {
  font-family: 'Metric';
  font-weight: 500;
  font-style: normal;
  src: url(/jf-fitness/static/media/Metric-Medium.fdc94e09.otf) format("opentype");
}

@font-face {
  font-family: 'Metric';
  font-weight: 600;
