  src: url(/jf-fitness/static/media/Metric-Semibold.8f3fd8c0.otf) format("opentype");
}

@font-face {
  font-family: 'Metric';
  font-weight: 800;
  font-style: normal;
  src: url(/jf-fitness/static/media/Metric-Black.608c5296.otf) format("opentype");
}


body {
  margin: 0;
  font-family: "Metric", 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #121212;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

